<template>
	<view>
		<customNavbar :title="data.name" />
		<view class="order-list m-t-10" >
			<view class="listTop viewFlex p-10">
				<view class="avatar-img border-radius-10 pull-left oh text-center">
					<img src="https://oss-img.mengzuhao.cn/school/avatar_4/七秀正太.png" width="88" height="110">
				</view>
				
				<view class="listText pull-left m-l-10" style="width: 98%; flex: 1 1 0%;">
					<h3 class="listTitle">
						<div class="account-item-game-icon" style="background-image: url(&quot;https://oss-img.mengzuhao.cn/account-game-icon.png&quot;); background-repeat: no-repeat; background-size: cover;">
							{{data.gameName}}
						</div>
						{{data.name}}
					</h3>
					<view class="listCon m-t-5">
						<span>{{data.zoneName}}</span>
						<text class="m-l-5">{{data.schoolName}}/{{data.shapeName}}</text>
					</view>
					<view class="userId m-t-5">账号ID：{{data.id}}</view>
					<div class="order-list-cost m-t-5">
						<span class="h">
							¥ 222/时
						</span> 
					</div>
				</view>
			</view>
		</view>
		
		<view class="order-parameter bg-white-shadow p-10 m-t-20">
			<view class="m-t-10 cumulative">
				<text>累计收入：</text>
				<text class="order-number">0.00元</text>
			</view>
			<view class="m-t-10">
				<text>累计收入：</text>
				<text class="desc">0.00元</text>
			</view>
			<view class="m-t-10">
				<text>出租次数：</text>
				<text class="desc">0.00元</text>
			</view>
			
			<view class="m-t-10">
				<text>游戏账号：</text>
				<text class="desc">{{data.account}}</text>
			</view>
			<view class="m-t-10">
				<text>游戏密码：</text>
				<text class="desc">{{data.password}}</text>
				<text class="click-right-em" @click="changePasswordShow = true">修改密码</text>
			</view>
		</view>
		
		<view class="btn-fixed-bottom">
			<u-button @click="$utils.handleNavigate('/pages/user/rent/release?id=' + data.id)">
				修改账号
			</u-button>
		</view>
		<u-popup mode="center" width="500" v-model="changePasswordShow" border-radius="14">
			<view class="changePasswordPopup">
				<h3 class="m-b-20" style="font-size: 16px; font-weight: 400; color: rgb(51, 51, 51);">修改密码</h3>
				<u-input border-color="#f4f4f4" :border="true" placeholder="请输入新密码" v-model="password"></u-input>
				<view class="viewFlex m-t-10">
					<u-button @click="changePasswordShow = false, password = ''">取消</u-button>
					<u-button type="success" @click="changePasswordClick">确定修改</u-button>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	import {
		statistics,
		changePassword,
	} from "@/api/account"
	export default {
		data() {
			return{
				data: {},
				changePasswordShow: false,
				password: ""
			}
		},
		onLoad(options) {
			let that = this
			if(options.id){
				this.id = options.id
				statistics(options.id).then(res=>{
					if(res.code == 1){
						that.data = res.data
					}
				})
			} 
		},
		methods:{
			changePasswordClick(){
				let that = this
					, password = this.password
				
				if(!password){
					this.$throw("请输入密码")
				}
				if(password == this.data.password){
					this.$throw("新密码不得与旧密码相同")
				}
				changePassword({
					password: this.password,
					id: this.id,
				}).then(res=>{
					this.$u.toast(res.msg);
					if(res.code == 1){
						that.data.password = password
						that.changePasswordShow = false
						that.password = ''
					}
				})
			}
		}
	}
</script>

<style lang="scss">
	.changePasswordPopup{
		text-align: center;
		padding: 20rpx;
	}
</style>